import React, { Component } from 'react';
import './App.css';
import Representative from './components/Representative/Representative';
import Senator from './components/Senator/Senator';

const selections = ['select1', 'select2'];

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      selectClasses: '',
      backKey: '',
      central: ''
    };
    this.expander = this.expander.bind(this);
    this.contractor = this.contractor.bind(this);
  };

  expander(type) {
    this.setState({ selectClasses: ' hide', backKey: ' appear' }, () => {
      selections.forEach((element) => {
        document.getElementById(element).onclick = null;
        document.getElementById(element).ondblclick = null;
        setTimeout( () => {
          document.getElementById(element).style.zIndex = -1;
        }, 1000);
      });
      document.getElementById("back").onclick = this.contractor;
      document.getElementById("back").ondblclick = this.contractor;
      document.getElementById("central").removeAttribute("style");
    });
    if (type === "rep"){
      this.setState({ central: <Representative /> });
    } else {
      this.setState({ central: <Senator /> });
    }
  };

  contractor(e){
    this.setState({ selectClasses: '', backKey: '' }, () => {
      selections.forEach( (element) => {
        document.getElementById(element).onclick = this.expander;
        document.getElementById(element).ondblclick = this.expander;
        document.getElementById(element).removeAttribute("style");
      });
      setTimeout( () => {
        document.getElementById("central").style.zIndex = -1;
      }, 1000);
      document.getElementById("back").onclick = null;
      document.getElementById("back").ondblclick = null;
    });
    this.setState({ central: '' });
  }

  render() {
    return (
      <div className="App">
        <div className="body">
          <div id="back" className={"back" + this.state.backKey}></div>
          <div id="select1" className={"select" + this.state.selectClasses} onClick={(() => this.expander("rep"))} onDoubleClick={this.expander}>Representative David Cicilline</div>
          <div id="select2" className={"select" + this.state.selectClasses} onClick={(() => this.expander("sen"))} onDoubleClick={this.expander}>Senator Jack Reed</div>
          <div id="select3" className={"warning select" + this.state.selectClasses}>Note: access to Twitter and Facebook must be enabled.</div>
          <div id="central" className={"central" + this.state.backKey} style={{zIndex: -1}}>{this.state.central}</div>
        </div>
      </div>
    );
  }
}

export default App;
